import React, {useState} from 'react';
import axios from 'axios';
import './App.css';
import "bootstrap/dist/js/bootstrap"
import "bootstrap/dist/css/bootstrap.css"

const Form = () => {
    const [userDetails, setUserDetails] = useState({
        firstName: '',
        lastName: '',
        email: '',
        phone: "",
        address: "",
        hearAbout: "",
        occupation: "",
        userMessage: ""
    })

    const formValues = (event) => {
        setUserDetails({
            ...userDetails,
            [event.target.name]: event.target.value
        })
    }
    const register = async (event) => {
        event.preventDefault();
        // When sending data from Front to Back, it should be sent as JSOn format
        const body = JSON.stringify({
            firstName: userDetails.firstName,
            lastName: userDetails.lastName,
            email: userDetails.email,
            phone: userDetails.phone,
            address: userDetails.address,
            hearAbout: userDetails.hearAbout,
            occupation: userDetails.occupation,
            userMessage: userDetails.userMessage
        })
        const res = await axios.post("/api/", body , {
            headers: {
                'Content-type':'application/json'
            }
        })
        setUserDetails({
            ...userDetails,
            message: res.data.message
        })
    }

    return (
        <div className="container">
            <h1 className="text-center my-4">The registration form</h1>
            <form onSubmit={register}>
                <div className="row form-group">
                    <div className="col">
                        <label htmlFor="firstname">First Name</label>
                        <input type="text" name="firstName" id="firstname" className="form-control" placeholder="First name" onChange={formValues}/>
                    </div>
                    <div className="col">
                        <label htmlFor="lastname">Last Name</label>
                        <input type="text" name="lastName" className="form-control" id="lastname" placeholder="Last name" onChange={formValues}/>
                    </div>
                </div>

                <div className="form-group">
                    <label htmlFor="email">Email address</label>
                    <input type="email" name="email" className="form-control" id="email" placeholder="Enter email" onChange={formValues}/>
                </div>

                <div className="form-group">
                    <label htmlFor="phone">Phone Number</label>
                    <input type="tel" name="phone" className="form-control" id="phone" onChange={formValues}/>
                </div>

                <div className="form-group">
                    <label htmlFor="address">Address</label>
                    <input type="string" name="address" className="form-control" id="address" placeholder="Address" onSelect={formValues}/>
                </div>

                <div className="form-group">
                    <label htmlFor="hear-about">How did you hear about us</label>
                    <select defaultValue={'default'} className="form-control" name="hearAbout" id="hear-about" onChange={formValues}>
                        <option value="default" disabled>Select Platform</option>
                        <option value="Facebook">Facebook</option>
                        <option value="LinkedIn">LinkedIn</option>
                        <option value="Google">Google</option>
                        <option value="Friends">Friends</option>
                        <option value="Instagram">Instagram</option>
                    </select>
                </div>

                <div className="form-group">
                    <label htmlFor="occupation">Occupation</label>
                    <select defaultValue={'default'} className="form-control" name="occupation" id="occupation" onChange={formValues}>
                        <option value="default" disabled>Select Profession</option>
                        <option value="Student">Student</option>
                        <option value="Freelancer">Freelancer</option>
                    </select>
                </div>

                <div className="form-group">
                    <label htmlFor="userMessage">Message</label>
                    <input type="text" name="userMessage" className="form-control" id="userMessage"
                           placeholder="name@example.com" onChange={formValues}/>
                </div>
                <button type="submit" className="btn btn-primary">Submit</button>
                {userDetails.message ?
                    <h1 className="resultMessage">{userDetails.message}</h1> :
                    null}
            </form>
        </div>
    );
}

export default Form;
